import React from 'react';
import { default as MuiTooltip, TooltipProps } from '@mui/material/Tooltip';

const Tooltip = React.forwardRef<HTMLElement, TooltipProps>( ( props, ref ) => {
	return (
		<MuiTooltip
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TooltipProps>;

export default Tooltip;
